<template>
    <div id="personalInfoPart">
        <v-header :title="title"></v-header>
        <div>
            <div class="row">
                <div class="col-lg-12">
                    <div class="panel panel-default">
                        <div class="panel-body">
                            <div id="toolbar">
                                <button id="dispatcher" class="btn btn-primary"   @click="doUpdate">
                                    <i class="glyphicon  glyphicon-edit"></i> 
                                </button>
                                <button  class="btn btn-info"  @click="doCreate">
                                    <i class="glyphicon  glyphicon-plus"></i> 
                                </button>
                                <label class="btn btn-primary">
                                     <input type="file" style="display: none;" id="personalInfo-upload" accept=".xls?" multiple>
                                </label>
                                <button  class="btn btn-info"  @click="ViewUploadResult">
                                    <i class="glyphicon glyphicon-eye-open"></i> 
                                </button>
                                <button  class="btn btn-danger" @click="doDelete">
                                    <i class="glyphicon  glyphicon-remove"></i> 
                                </button>
                                <select class="btn" style="border: 1px solid #30a5ff;" v-model.number="searchKeys.dateRange" @change="changeByDateRange">
                                    <option value="1"></option>
                                    <option value="3"></option>
                                    <option value="6"></option>
                                    <option value="12"></option>
                                </select>
                            </div>
                            <table id="table"    data-show-refresh="true" data-show-toggle="true" data-show-columns="true" data-search="true" data-select-item-name="toolbar1" data-pagination="true"  data-sort-name="create_date" data-sort-order="desc"
                                   data-page-size="5" data-page-list="[5,10,20,50]"  data-toolbar="#toolbar" data-advanced-search="true" data-id-table="advancedTable"
                                   data-side-pagination="client" data-striped="true" data-single-select="true"
                            >
                            </table>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="modal fade" id="uploadResultModal" tabindex="-1" role="dialog" aria-labelledby="uploadResultModalLabel" aria-hidden="true">
            <div class="modal-dialog modal-lg">
                <div class="modal-content">
                    <div class="modal-header">
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span></button>
                        <h4 class="modal-title"></h4>
                    </div>
                    <div class="modal-body">
                        <table id="uploadResultTable"  data-pagination="true"  data-sort-order="desc" data-sort-name="create_date"
                               data-page-size="10"
                               data-side-pagination="client" data-striped="true"
                               data-height="400"
                        >
                        </table>
                    </div>
                </div><!-- /.modal-content -->
            </div><!-- /.modal-dialog -->
        </div><!-- /.modal -->

        <v-modal vmodal-id="personalInfoModal" vmodal-labelledby="myModalLabel" :vmodal-title="personalInfo.title" :vmodal-submit="doCreateOrUpdate">
            <div class="fixed-height">
                <div class="form-group margin0">
                    <label class="col-md-3 control-label" for="personalInfo-route">:</label>
                    <div class="col-md-9">
                        <input class="form-control" placeholder="" id="personalInfo-route" type="text"  v-model.trim="personalInfo.route" >
                    </div>
                </div>
                <div class="form-group margin0">
                    <label class="col-md-3 control-label" for="personalInfo-driverName">:</label>
                    <div class="col-md-9">
                        <input class="form-control" placeholder="" id="personalInfo-driverName" type="text"  v-model.trim="personalInfo.driverName" required>
                    </div>
                </div>
                <div class="form-group margin0">
                    <label class="col-md-3 control-label" for="personalInfo-carNum">:</label>
                    <div class="col-md-9">
                        <input class="form-control" placeholder="" id="personalInfo-carNum" type="text"  v-model.trim="personalInfo.carNum" required>
                    </div>
                </div>
                <div class="form-group margin0">
                    <label class="col-md-3 control-label" for="personalInfo-telephoneNum">:</label>
                    <div class="col-md-9">
                        <input class="form-control" placeholder="" id="personalInfo-telephoneNum" type="text"  v-model.trim="personalInfo.telephoneNum" required>
                    </div>
                </div>
                <div class="form-group margin0">
                    <label class="col-md-3 control-label" for="personalInfo-carType">:</label>
                    <div class="col-md-9">
                        <input class="form-control" placeholder="" id="personalInfo-carType" type="text"  v-model.trim="personalInfo.carType" required>
                    </div>
                </div>
                <div class="form-group margin0">
                    <label class="col-md-3 control-label" for="personalInfo-carTeam">:</label>
                    <div class="col-md-9">
                        <input class="form-control" placeholder="" id="personalInfo-carTeam" type="text"  v-model.trim="personalInfo.carTeam" >
                    </div>
                </div>
                <div class="form-group margin0">
                    <label class="col-md-3 control-label" for="personalInfo-bankNum">:</label>
                    <div class="col-md-9">
                        <input class="form-control" placeholder="" id="personalInfo-bankNum" type="text"  v-model.trim="personalInfo.bankNum" >
                    </div>
                </div>
                <div class="form-group margin0">
                    <label class="col-md-3 control-label" for="personalInfo-orderRate">:</label>
                    <div class="col-md-9">
                        <input class="form-control" placeholder="" id="personalInfo-orderRate" type="text"  v-model.trim="personalInfo.orderRate" >
                    </div>
                </div>
                <div class="form-group margin0">
                    <label class="col-md-3 control-label" for="personalInfo-serviceAbility">:</label>
                    <div class="col-md-9">
                        <input class="form-control" placeholder="" id="personalInfo-serviceAbility" type="text"  v-model.trim="personalInfo.serviceAbility" >
                    </div>
                </div>
                <div class="form-group margin0">
                    <label class="col-md-3 control-label" for="personalInfo-address">:</label>
                    <div class="col-md-9">
                        <input class="form-control" placeholder="" id="personalInfo-address" type="text"  v-model.trim="personalInfo.address" >
                    </div>
                </div>
                <div class="form-group margin0">
                    <label class="col-md-3 control-label" for="personalInfo-description">:</label>
                    <div class="col-md-9">
                        <input class="form-control" placeholder="" id="personalInfo-description" type="text"  v-model.trim="personalInfo.description" >
                    </div>
                </div>
            </div>
        </v-modal>
    </div>
</template>
<style>
</style>
<script>
    let $table,$modal,$uploadResultTable,$uploadResultModal,searchKeys={dateRange:3},
    commonColumns=[
    {
        field: 'route',
        title: '',
        sortable: true,
        align: 'center'
    },
    {
        field: 'driverName',
        title: '',
        sortable: true,
        align: 'center'
    },
    {
        field: 'carNum',
        title: '',
        sortable: true,
        align: 'center'
    },
    {
        field: 'telephoneNum',
        title: '',
        sortable: true,
        align: 'center'
    },
    {
        field: 'carType',
        title: '',
        sortable: true,
        align: 'center'
    },
    {
        field: 'carTeam',
        title: '',
        sortable: true,
        align: 'center'
    },
    {
        field: 'bankNum',
        title: '',
        sortable: true,
        align: 'center'
    },
    {
        field: 'orderRate',
        title: '',
        sortable: true,
        align: 'center'
    },
    {
        field: 'serviceAbility',
        title: '',
        sortable: true,
        align: 'center'
    },
    {
        field: 'address',
        title: '',
        sortable: true,
        align: 'center'
    },
    {
        field: 'description',
        title: '',
        sortable: true,
        align: 'center'
    },
    ];
    let timeFormatter=(row, index )=>{
        return (new Date(row)).format("yyyy-MM-dd hh:mm:ss");
    },
    refreshTable=()=>{
        $table.bootstrapTable('refresh');
    },
    getSelections=()=>{
        let selections=$table.bootstrapTable('getSelections');
        if(selections.length===0) throw new Error("0")
        return selections;
    },
    statusStyle= (value, row, index, field)=> {
      return {
        classes: '',
        css: {"color": value==""?"red":""}
      };
    }
    $(document).on('change', '#personalInfo-upload', function() {
            let $this=$(this),files=$this.get(0).files;
            let body = new FormData();
            $.each(files,(i,file)=>{
               body.append('file[]', file);
            })
            upload('/backend/personalInfo/upload',body).then(v=>{
                $this.val("");
                if(v && v.status===0){
                    alertify.success(v.message);
                    refreshTable();
                }else{
                    throw new Error(v.message)
                }
            }).catch(error=>{
                alertify.error(":"+error.message);
            });
    });
    function ajaxRequest(params) {
        formPost("/backend/personalInfo/all",Object.assign(params.data,searchKeys)).then(v=>{
             params.success(v)
        }).catch(e=>{
            alertify.error(e.message)
        })
    }
    function ajaxUploadResultRequest(params) {
        formPost("/backend/uploadResult/view/PERSONALINFO",Object.assign(params.data,searchKeys)).then(v=>{
             params.success(v)
        }).catch(e=>{
            alertify.error(e.message)
        })
    }
    function responseHandler(res) {
        return res;
    }

    let initTable=()=>{
              $table=$("#table")
              $table.bootstrapTable({
                 ajax: ajaxRequest,
                 responseHandler:responseHandler,
                 columns: [
                  [{
                      field: 'state',
                      align: 'center',
                      checkbox:true,
                  },
                  {
                      field: 'id',
                      title: 'ID',
                      sortable: true,
                      align: 'center',
                  },
                  {
                      field: 'updateDate',
                      title: '',
                      sortable: true,
                      align: 'center',
                      formatter: timeFormatter,
                  },
                  ...commonColumns,
                  {
                      field: 'createDate',
                      title: '',
                      sortable: true,
                      align: 'center',
                      formatter: timeFormatter,
                  },
                  ]
          ],
          onLoadSuccess: function(){

          },
          onLoadError: function(){

          }
      });
    };
    let initUploadResultTable=()=>{
              $uploadResultTable=$("#uploadResultTable")
              $uploadResultTable.bootstrapTable({
                 ajax: ajaxUploadResultRequest,
                 columns: [
                  [{
                      field: 'status',
                      title: '',
                      align: 'center',
                      sortable: true,
                      cellStyle:statusStyle
                  },
                  {
                      field: 'createDate',
                      title: '',
                      align: 'center',
                      formatter: timeFormatter,
                      sortable: true,
                  },
                  ...commonColumns
                  ]
          ],
      });
    };
    export default{
        data(){
            return{
                searchKeys: searchKeys,
                title:'',
                personalInfo:{
                    title:"",
                    route:"",
                    driverName:"",
                    carNum:"",
                    telephoneNum:"",
                    carType:"",
                    carTeam:"",
                    bankNum:"",
                    orderRate:"",
                    serviceAbility:"",
                    address:"",
                    description:"",
                }
            }
        },
        methods:{
            changeByDateRange(){
               refreshTable()
            },
            doCheck(){
                let arr=getSelections();
                if(arr.length>1){
                    throw new Error("");
                }else if(arr.length===0){
                    throw new Error("");
                }
                return arr;
            },
            doCreateOrUpdate(){
                post("/backend/personalInfo/save",this.personalInfo).then(v=>{
                    if(v.status==0){
                        alertify.success(v.message);
                        $modal.modal("hide");
                        refreshTable();
                    }else{
                        throw new Error(v.message)
                    }
                }).catch(e=>{
                    alertify.error("")
                })
            },
            ViewUploadResult(){
                $uploadResultTable.bootstrapTable('refresh');
                $uploadResultModal.modal("show")
            },
            doCreate(){
                $.each(Object.keys(this.personalInfo),(index,v)=>{this.personalInfo[v]=''})
                this.personalInfo.title="";
                $modal.modal("show");
            },
            doUpdate(){
                try{
                    let arr=this.doCheck();
                    let el={...arr[0],title:''};
                    Object.assign(this.personalInfo,el);
                    $modal.modal("show");
                }catch(e){
                    alertify.error(e.message)
                }
            },
            doDelete(){
                try{
                    let arr=this.doCheck();if(!arr) return;
                     post("/backend/personalInfo/delete/"+(arr[0].id)).
                     then(body=>{
                        if(body && body.status==0){
                            alertify.success(body.message);
                            $table.bootstrapTable('refresh');
                        }else{
                            throw new Error(body.message)
                        }
                     }).catch(()=>{
                         alertify.success("");
                     })
                }catch(e){
                    alertify.error(e.message)
                }
            }
        },
        mounted(){
            $modal=$("#personalInfoModal");
            $uploadResultModal=$("#uploadResultModal");
            initTable();
            initUploadResultTable();
        }
    }
</script>



